@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Permanent+Marker&display=swap);
:root{
    --colorDark: #03071E;
    --colorBrownDark: #370617;
    --colorBrown: #6A040F;
    --colorBrownLigth: #9D0208;
    --colorOrangeDark: #D00000;
    --colorOrange: #DC2F02;
    --colorOrangeLigth: #E85D04;
    --colorYellowDark: #F48C06;
    --colorYellow: #FAA307;
    --colorYellowLight: #FFBA08;
}
body, html{
    font-size: 16px;
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif

}

body{
    background-color: #03071E;
    background-color: var(--colorDark);
    color:white;
}

a{
    color: #F48C06;
    color: var(--colorYellowDark);
    text-decoration: none;
    line-height: normal;
    line-height: initial;
}

.container{
    margin:0 1rem
}

.btn{
    background: #D00000;
    border: none;
    border-radius: .5rem;
    color: #FFBA08;
    color: var(--colorYellowLight);
    font-weight: 700;
    font-family: inherit;
    padding: .5rem 1rem;

}  
    .btn--primary{
        background: #F48C06;
        background: var(--colorYellowDark);
        color: #370617;
        color: var(--colorBrownDark);
    }

/* MAIN CONTENT */
.content{
    display: grid;
    grid-template-columns: 1fr 35%;
    margin: 2rem 0;
} 

/* SIDEBAR */
.sidebar{
    
    padding: 1rem;
    border-radius: .5rem;
}
.header{
    display: grid;
    grid-template-columns: 25% 1fr 15%;
    align-items: center;
}
    .header__brand{}
    .header__navbar{}
        .header__navbar ul{
            display: flex;
        }
        .header__navbar ul li{
            list-style: none;
            box-sizing: border-box;
            margin: 0 1rem 0 0;
        }
    .header__user{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
.slide{
    background-image: url(/eLigaFront/static/media/slide.7138c8c5.jpg);
    background-color: var(--colorOrange);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    margin: 0 -1rem;
    padding: 5rem 1rem;
    text-align: center;
}

.slide__tittle{
    font-family: 'Permanent Marker', cursive;
    font-size: 4rem;
    line-height: 0;
}
.rank__list{
    align-items: center;
    border-bottom: 1px solid var(--colorDark);
    display: grid;
    grid-template-columns: 3rem 1fr 1fr 3rem;
    grid-gap: 0 1rem;
    justify-content: center;
    margin: 0 0 1rem 0;
    padding: 0 0 1rem 0;
}

    .rank__bboy--profile{
        background: var(--colorOrangeDark);
        border: solid 1px white;
        grid-row: span 2;
        border-radius: 50%;
        box-sizing: border-box;
        height: 3rem;
        width: 3rem;
    }
    .rank__bboy--name{
        color: var(--colorYellowLight);
        font-size: 1.25rem;
        grid-column: span 2;
    }
    .rank__bboy--crew{
        grid-column: 2 / 3;
        grid-row: 2 / 3;
    }
    .rank__bboy--city{
        grid-column: 3 / 4;
        grid-row: 2 / 3;
    }
    .rank__bboys--point{
        background-color: var(--colorYellowLight);
        color: white;
        height: 3rem;
        width: 3rem;
        border-radius: 0.25rem;
        padding: .5rem;
        box-sizing: border-box;
        font-size: 1.5rem;
        grid-row: span 2;
    }


.title__rank{
    text-align: center;
}
.battle__content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem 2rem;
}

.battles{
    align-items: center;
    background: white;
    border-radius: 0.5rem;
    color: var(--colorDark);
    display: grid;
    grid-template-columns: 1fr 3rem 1fr;
    grid-gap: 1rem;
    justify-content: center;
    padding: 1rem 1.5rem;
}
    .battle__bboy{
        color: var(--colorBrownDark);
        font-size: 1.25rem;
    }
    .battle_vs{
        background: var(--colorYellow);
        color: white;
        height: 3rem;
        width: 3rem;
        border-radius: 0.25rem;
        padding: .5rem;
        box-sizing: border-box;
        font-size: 1.5rem;
    }
    .battle__des{
        grid-column: 1 / 3;
    }
    .battle__btn{
        grid-column: 3 / -1;
    }
